class MediaPlayer {
    constructor(config) {
        this.media = config.el
        this.plugins = config.plugins || []
        this._intPlugins()
    }
    _intPlugins() {
        const player = {
            play: () => this.play(),
            pause: () => this.pause(),
            media: this.media,
            get muted() {
                return this.media.muted
            },
            set muted(value) {
                this.media.muted = value
            }
        }
        this.plugins.forEach(plugin => {
            plugin.run(player)
        })
    }
    play() {
        this.media.play()
    }
    pause() {
        this.media.pause()
    }
    mute() {
        this.media.muted = true
    }
    unMute() {
        this.media.muted = false
    }
    togglePlay() {
        if (this.media.paused) {
            this.play()
        } else {
            this.pause()
        }
    }
    toggleMute() {
        this.media.muted = !this.media.muted
    }
}



export default MediaPlayer